<style>
@keyframes spin {
	0% {transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg);}
  100% {transform: rotateX(360deg) rotateY(360deg) rotateZ(360deg);}
}

@keyframes fixEye {
  0% {color: #ff0000;}
  50% {color: #00ff00;}
  100% {color: #ff0000;}
}

.logo {
	animation-name: spin;
	animation-duration: 2000ms;
	animation-iteration-count: infinite;
}

.v-icon {
  transform: scaleX(.9) scaleY(.9);
}

.v-icon:hover {
  transform: scaleX(1.5) scaleY(1.5);
}

::-webkit-scrollbar {
  width: 15px;
  background-color: #ff0000;
  display: none;
}

::-webkit-scrollbar-thumb {
  background-color: #00ff00;
}
</style>

<template>
  <v-app>
    <v-app-bar
      app
      color="primary"
      dark
    >
      <v-icon class="logo blue--text" x-large>
        mdi-cat
      </v-icon>

      GengTu.Upload

      <v-spacer></v-spacer>

      <v-btn
        href="https://github.com/HelloGwkki/GengTu"
        target="_blank"
        text
        icon
      >
        <v-icon>
          mdi-github
        </v-icon>
      </v-btn>
    </v-app-bar>

    <v-main>
      <v-layout
        row
        wrap
        align-center
        justify-center
        fill-height
      >
        <v-card
          elevation="24"
          width="85%"
        >
          <v-card-subtitle>
            GengTu.Upload
          </v-card-subtitle>
          <v-divider></v-divider>
          <v-card-text>
            <v-alert
              elevation="12"
              text
              type="warning"
            >
            由于 Gengtu 相册放在 Github 上，您提交 Gengtu 时需要一个 Github账号 。
            <br />
            另外，您可能需要 Cross Great Wall.
            </v-alert>

            <v-form 
              ref="form"
              v-model="formValid"
              lazy-validation
            >
              <v-text-field
                v-model="imageLink"
                :rules="imageLinkRule"
                label="图片链接（需为图片直链）"
                required
                prepend-icon="mdi-image"
              ></v-text-field>

              <v-textarea
                v-model="description"
                :rules="descriptionRule"
                counter="100"
                auto-grow
                clearable
                clear-icon="mdi-close"
                prepend-icon="mdi-comment"
                rows="5"
                label="图片描述（非必填）"
              ></v-textarea>

              <template>
                  <v-combobox
                    v-model="aboutPeople"
                    :items="peoples"
                    :search-input.sync="search"
                    :rules="aboutPeopleRule"
                    hint="最多五个人"
                    label="与此 Gengtu 有关的人"
                    multiple
                    required
                    prepend-icon="mdi-bash"
                  >
                    <template v-slot:no-data>
                      <v-list-item>
                        <v-list-item-content>
                          <v-list-item-title>
                            没有我们认为可能会出现的人。 按下 <kbd>Enter</kbd> 以新增 <strong> {{ search }} </strong>。
                          </v-list-item-title>
                        </v-list-item-content>
                      </v-list-item>
                    </template>
                  </v-combobox>
              </template>

              <v-text-field
                v-model="qqId"
                :rules="qqIdRule"
                label="QQ号"
                required
                prepend-icon="mdi-qqchat"
              ></v-text-field>

              <v-card-actions>
                <v-btn
                  :disabled="!formValid"
                  color="success"
                  class="mr-4"
                  @click="validate"
                >
                  提交
                </v-btn>

                <v-btn
                  color="error"
                  class="mr-4"
                  @click="reset"
                >
                  重置
                </v-btn>
              </v-card-actions>
            </v-form>
          </v-card-text>
        </v-card>
      </v-layout>
    </v-main>
  </v-app>
</template>

<script>
import tool from './assets/tool';

export default {
  name: 'app',

  data: () => ({
    formValid: false,
    imageLink:'',
    imageLinkRule: [
      v => !!v || '图片链接不能为空',
      v => /^((https|http)?:\/\/)[^\s]+(\.gif|jpeg|png|jpg)+/.test(v) || '链接格式有误'
    ],
    description: '',
    descriptionRule: [
      v => !(tool.awesome(v).length > 100) || '字数不能大于100'
    ],
    peoples: ["空", "彩虹酱", "NoName", "HelloGwkki", "Dundun", "蓝梦", "宇宙", "shift8", "霜啸"],
    aboutPeople: [],
    search: null,
    aboutPeopleRule: [
      v => !tool.awesome(v).length <= 0 || '必须选择此 Gengtu 的相关人员',
      v => !(tool.awesome(v).length > 5) || '不能选择 5 个人以上'
    ],
    qqId: '',
    qqIdRule: [
      v => !!v || ' QQ号 不能为空',
      v => /[1-9]([0-9]{4,10})/.test(v) || 'QQ号格式有误'
    ]
  }),

  methods: {
    validate(){
      this.$refs.form.validate();
      if (this.$refs.form.validate()){
        console.log(this);
        window.location.href = "https://github.com/HelloGwkki/GengTu/issues/new?" +
                               "title=GengtuUpload" +
                               "&body=" +
                               "<!--Please%20submit.%20DONT%20EDIT%20AGAIN.-->"+
                               "%0A%0A" +
                               "[IMAGE LINK]%0A" + this.$data.imageLink +
                               "%0A%0A" +
                               "[DESCRIPTION]%0A" + this.$data.description +
                               "%0A%0A" +
                               "[PEOPLES]%0A" + this.$data.aboutPeople +
                               "%0A%0A" +
                               "[QQ ID]%0A" + this.$data.qqId +
                               "%0A%0A%0AOwO";
      }
    },
    reset(){
      this.$refs.form.reset()
    }
  }
};
</script>
